const helpers = (context) => {
  const i18nFilter = context.$filter('i18n');
  const cardPrefixMask =
    '\u25CF\u25CF\u25CF\u25CF \u25CF\u25CF\u25CF\u25CF \u25CF\u25CF\u25CF\u25CF ';
  return {
    /**
     * @description
     * Sets a prefix for a number property which contains last
     * 4 characters for a card number
     *
     * @name maskCardNumber
     * @type {function}
     * @param {string} number
     * @returns {string} prefix + number
     */
    maskCardNumber: (number) => cardPrefixMask + number,

    /**
     * @description
     * Handles one or multiple errors messages, mapping error key with propper message
     *
     * @name handleErrors
     * @type {function}
     * @param {array} errors
     * @returns {string} message
     */
    handleErrors: (errors) => {
      if (!errors) {
        return null;
      }
      return errors.map((e) => i18nFilter(e)).join(' ');
    },
  };
};
export default helpers;




